<template>
  <v-container>
    <v-row>
        <v-col cols="12">
          <h1 class="display-2 font-weight-bold mb-3">Softball Dashboard</h1>
        </v-col>
    </v-row>

    <v-row>
        <v-col cols="12">
          <h3>Men's Stats</h3>
        </v-col>
    </v-row>

    <v-row class="pt-5">
      <v-col cols="12" md="3" lg="2">
        <v-card>
          <v-card-text style="text-align: center">
            <div>Record</div>
            <div class="text-h4 text--primary pt-6">{{menwins}} - {{menlosses}}</div>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Home Runs</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{meninsidehrs}}</td>
                  <td>Inside the Park</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{menoutsidehrs}}</td>
                  <td>Over the Fence</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{mentotalhrs}}</td>
                  <td>Total Home Runs</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Run Differential</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{menrunsfor}}</td>
                  <td>Runs For</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{menrunsagainst}}</td>
                  <td>Runs Against</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="12" lg="5">
        <v-card>
          <v-card-text>
            <div>Next Game Day</div>
            <v-data-table
              :headers="headers"
              :items="menupcoming"
              :sort-by="['date', 'time']"
              :sort-desc="[false, false]"
              multi-sort
              disable-pagination
              hide-default-footer
              :loading="loadMenGames"
              loading-text="Loading...Please wait"
              no-data-text="Season complete!"
            ></v-data-table>
          </v-card-text>
          <v-card-actions>
            <v-btn color="button_color" text to="/2023-Spring-Men">Full Schedule</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="pt-5">
      <v-col cols="12">
        <hr/>
      </v-col>
    </v-row>

    <v-row>
        <v-col cols="12">
          <h3>Coed Stats</h3>
        </v-col>
    </v-row>

    <v-row class="pt-5">
      <v-col cols="12" md="3" lg="2">
        <v-card>
          <v-card-text style="text-align: center">
            <div>Record</div>
            <div class="text-h4 text--primary pt-6">{{coedwins}} - {{coedlosses}}</div>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Home Runs</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{coedinsidehrs}}</td>
                  <td>Inside the Park</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{coedoutsidehrs}}</td>
                  <td>Over the Fence</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{coedtotalhrs}}</td>
                  <td>Total Home Runs</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Run Differential</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{coedrunsfor}}</td>
                  <td>Runs For</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{coedrunsagainst}}</td>
                  <td>Runs Against</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="12" lg="5">
        <v-card>
          <v-card-text>
            <div>Next Game Day</div>
            <v-data-table
              :headers="headers"
              :items="coedupcoming"
              :sort-by="['date', 'time']"
              :sort-desc="[false, false]"
              multi-sort
              disable-pagination
              hide-default-footer
              no-data-text="Season complete!"
              :loading="loadCoEdGames"
              loading-text="Loading...Please wait"
            ></v-data-table>
          </v-card-text>
          <v-card-actions>
            <v-btn color="button_color" text to="/2023-Spring-CoEd">Full Schedule</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>

  </v-container>
</template>

<script>
  import moment from 'moment';
  import axios from 'axios';

  export default {
    name: 'HelloWorld',
    data() {
      return {
      headers: [
        { text: "Game Date", value: "date"},
        { text: "Opponent", value: "opponent" },
        { text: "Game Time", value: "time", align: "center" },
        { text: "Field", value: "field", align: "center" },
      ],
        mensgames: [],
        coedgames: [],
        menupcoming: [],
        coedupcoming: [],
        menwins: 0,
        menlosses: 0,
        menrunsfor: 0,
        menrunsagainst: 0,
        meninsidehrs: 0,
        menoutsidehrs: 0,
        mentotalhrs: 0,
        coedwins: 0,
        coedlosses: 0,
        coedrunsfor: 0,
        coedrunsagainst: 0,
        coedinsidehrs: 0,
        coedoutsidehrs: 0,
        coedtotalhrs: 0,
        loadMenGames: true,
        loadCoEdGames: true,
      }
    },
    mounted() {
      this.loadData();
    },
    methods: {
      async loadData() {
        await axios.get(`https://gsx2json.com/api?api_key=${process.env.VUE_APP_API_KEY}&columns=false&id=16klK3voGD-oCUJfuS6Q2E74kHD4gm-dNwueeUG5IGis&sheet=Current`)
          .then(response => {
            let allGames = response.data.rows;
            this.mensgames = allGames.filter(item => {return item.type == "Men"});
            this.coedgames = allGames.filter(item => {return item.type == "CoEd"});

            for (let i = 0; i < this.coedgames.length; i++) 
            {
              
              if(this.coedgames[i].results == "W") this.coedwins++;
              if(this.coedgames[i].results == "L") this.coedlosses++;
              if(this.coedgames[i].our_score != undefined) this.coedrunsfor += this.coedgames[i].our_score;
              if(this.coedgames[i].their_score != undefined) this.coedrunsagainst += this.coedgames[i].their_score;
              if(this.coedgames[i].inside_hrs != undefined) 
              {
                this.coedinsidehrs += this.coedgames[i].inside_hrs;
                this.coedtotalhrs += this.coedgames[i].inside_hrs;
              }
              
              if(this.coedgames[i].outside_hrs != undefined) 
              {
                this.coedoutsidehrs += this.coedgames[i].outside_hrs;
                this.coedtotalhrs += this.coedgames[i].outside_hrs;
              }
            }

            for (let i = 0; i < this.mensgames.length; i++) 
            {
              if(this.mensgames[i].results == "W") this.menwins++;
              if(this.mensgames[i].results == "L") this.menlosses++;
              if(this.mensgames[i].our_score != undefined) this.menrunsfor += this.mensgames[i].our_score;
              if(this.mensgames[i].their_score != undefined) this.menrunsagainst += this.mensgames[i].their_score;
              if(this.mensgames[i].inside_hrs != undefined) 
              {
                this.meninsidehrs += this.mensgames[i].inside_hrs;
                this.mentotalhrs += this.mensgames[i].inside_hrs;
              }
              
              if(this.mensgames[i].outside_hrs != undefined) 
              {
                this.menoutsidehrs += this.mensgames[i].outside_hrs;
                this.mentotalhrs += this.mensgames[i].outside_hrs;
              }
            }

            //Get Current Moment
            const currentMoment = moment();
            //Set Moment to current date
            const currentDay = moment().set({'year': currentMoment.year(), 'month': currentMoment.month(), 'date': currentMoment.date(), 'hour': 1, 'minute': 0, 'second': 0});
            currentDay.subtract(1, 'days');
            let nextMenGame;

            for (let i = 0; i < this.mensgames.length; i++) 
            {
              let myGameDate = moment(this.mensgames[i].date, "MM/DD/YYYY");
              if(currentDay.isSameOrBefore(myGameDate))
              {
                nextMenGame = myGameDate;
                break;
              }
            }

            for (let i = 0; i < this.mensgames.length; i++) 
            {
              if(nextMenGame != undefined && nextMenGame.isSame(moment(this.mensgames[i].date, "MM/DD/YYYY")))
              {
                this.menupcoming.push(this.mensgames[i]);
              }
            }

            this.loadMenGames = false;

            let nextCoEdGame;
            for (let i = 0; i < this.coedgames.length; i++) 
            {
              let myGameDate = moment(this.coedgames[i].date, "MM/DD/YYYY");
              if(currentDay.isSameOrBefore(myGameDate))
              {
                nextCoEdGame = myGameDate;
                break;
              }
            }

            for (let i = 0; i < this.coedgames.length; i++) 
            {
              if(nextCoEdGame != undefined && nextCoEdGame.isSame(moment(this.coedgames[i].date, "MM/DD/YYYY")))
              {
                this.coedupcoming.push(this.coedgames[i]);
              }
            }

            this.loadCoEdGames = false;
          })
          .catch(error => {
            console.error(error);
          });
      },
    }
  }
</script>
